import wx from "weixin-js-sdk";
import { Dialog } from "vant";
import { getJSSDKSign } from "./api/base";
import { PAGE_BASE_URL } from "./config";

export async function initJsSDK() {
  try {
    const data = await getJSSDKSign();
    const { appId, timestamp, nonceStr, signature } = data;
    wx.config({
      debug: false, // 
      appId, // 
      timestamp, // 
      nonceStr, // 
      signature, // 1
      jsApiList: [
        // JS2
        "onMenuShareTimeline",
        "onMenuShareAppMessage",
        "onMenuShareQQ",
        "onMenuShareWeibo",
        "onMenuShareQZone",
        "chooseWXPay",
        "getLocation",
        "openLocation",
        "scanQRCode",
        "chooseImage",
        "uploadImage",
        "previewImage",
        "getLocalImgData",
        "startRecord",
        "stopRecord",
        "onVoiceRecordEnd",
        "uploadVoice"
      ]
    });

    wx.ready(() => {
      setShare({});
    });

    wx.error(res => {
      console.log("wx.error: ", res);
    });
  } catch (e) {
    Dialog.confirm({
      title: "",
      message: "JSSDK",
      confirmButtonText: ""
    })
      .then(() => {
        initJsSDK();
      })
      .catch(() => {
        // on cancel
      });
  }
}

export function setShare({
  title = "", // 
  desc = "",
  link = PAGE_BASE_URL, // 
  imgUrl = PAGE_BASE_URL + "share.jpg" // 
}) {
  const infoThunk = str => obj =>
    console.log(`${str} + ${link} + ${JSON.stringify(obj)}`);

  wx.onMenuShareAppMessage({
    title,
    link,
    imgUrl,
    desc,
    cancel: infoThunk("onMenuShareAppMessage cancel"),
    fail: infoThunk("onMenuShareAppMessage fail"),
    success: infoThunk("onMenuShareAppMessage success")
  });

  wx.onMenuShareTimeline({
    title,
    link,
    imgUrl,
    desc,
    cancel: infoThunk("onMenuShareTimeline cancel"),
    fail: infoThunk("onMenuShareTimeline fail"),
    success: infoThunk("onMenuShareTimeline success")
  });
}
